<template>
  <div class="container">
    <h1>Notas</h1>
    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown = 0"
      @dismiss-count-down="countDownChanged"
    >
      <!-- <p>This alert will dismiss after {{ dismissCountDown }} seconds...</p> -->
      <p>{{ mensaje.texto }}</p>
      <b-progress
        variant="warning"
        :max="dismissSecs"
        :value="dismissCountDown"
        height="4px"
      ></b-progress>
    </b-alert>

    <form @submit.prevent="agregarNota()" v-if="!editar">
      <h3>Agregar nueva Nota</h3>
      <div class="form-group">
        <input
          type="text"
          v-model="item.nombre"
          class="form-control"
          placeholder="Nombre"
        />
      </div>
      <div class="form-group">
        <input
          type="text"
          v-model="item.descripcion"
          class="form-control"
          placeholder="Descripcion"
        />
      </div>
      <div class="form-group">
        <b-button type="submit" variant="success" class="btn-block mb-4"
          >Agregar</b-button
        >
      </div>
    </form>

    <form @submit.prevent="editarNota(itemEdit)" v-if="editar">
      <h3>Editar Nota</h3>
      <div class="form-group">
        <input
          type="text"
          v-model="itemEdit.nombre"
          class="form-control"
          placeholder="Nombre"
        />
      </div>
      <div class="form-group">
        <input
          type="text"
          v-model="itemEdit.descripcion"
          class="form-control"
          placeholder="Descripcion"
        />
      </div>
      <div class="form-group">
        <b-button type="submit" variant="success" class="mb-4">Editar</b-button>
        <b-button
          type="button"
          variant="secondary"
          @click="editar = false"
          class="ml-2 mb-4"
          >Cancelar</b-button
        >
      </div>
    </form>

    <table class="table">
      <thead>
        <tr>
          <th>#</th>
          <th>Nombre</th>
          <th>Descripcion</th>
          <th>Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(nota, index) in notas" :key="index">
          <td>{{ nota._id }}</td>
          <td>{{ nota.nombre }}</td>
          <td>{{ nota.descripcion }}</td>
          <td>
            <b-button
              variant="secondary"
              @click="editarNotaForm(nota._id)"
              class="mr-2"
              >Editar</b-button
            >
            <b-button variant="danger" @click="eliminarNota(nota._id)"
              >Eliminar</b-button
            >
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(["token"]),
  },
  data() {
    return {
      notas: [],
      dismissSecs: 5,
      dismissCountDown: 0,
      showDismissibleAlert: false,
      mensaje: {
        color: "",
        texto: "",
      },
      item: {
        nombre: "",
        descripcion: "",
      },
      itemEdit: {
        _id: "",
        nombre: "",
        descripcion: "",
      },
      editar: false,
    };
  },
  created() {
    this.listarNotas();
  },
  methods: {
    agregarNota() {
      const config = {
        headers: {
          token: this.token,
        },
      };
      this.axios
        .post("/nota", this.item, config)
        .then((res) => {
          this.notas.push(res.data);
          this.item.nombre = "";
          this.item.descripcion = "";
          this.mensaje.color = "success";
          this.mensaje.texto = "Nota creada satisfactoriamente";
          this.showAlert();
        })
        .catch((err) => console.log(err));
    },
    alerta() {
      this.mensaje.color = "success";
      this.mensaje.texto = "Probando Alerta";
      this.showAlert();
    },
    listarNotas() {
      const config = {
        headers: {
          token: this.token,
        },
      };
      this.axios
        .get("/nota", config)
        .then((res) => {
          this.notas = res.data;
        })
        .catch((err) => console.log(err));
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    },
    eliminarNota(id) {
      this.axios
        .delete(`/nota/${id}`)
        .then((res) => {
          const index = this.notas.findIndex(
            (item) => item._id === res.data._id
          );
          this.notas.splice(index, 1);
          this.mensaje.color = "success";
          this.mensaje.texto = "Nota Eliminada";
          this.showAlert();
        })
        .catch((err) => console.log(err));
    },
    editarNotaForm(id) {
      this.editar = true;
      this.axios
        .get(`/nota/${id}`)
        .then((res) => {
          this.itemEdit._id = res.data._id;
          this.itemEdit.nombre = res.data.nombre;
          this.itemEdit.descripcion = res.data.descripcion;
        })
        .catch((err) => console.log(err));
    },
    editarNota(item) {
      this.axios
        .put(`/nota/${item._id}`, item)
        .then((res) => {
          const index = this.notas.findIndex(
            (item) => item._id === res.data._id
          );
          this.notas[index].nombre = res.data.nombre;
          this.notas[index].descripcion = res.data.descripcion;
          this.mensaje.color = "success";
          this.mensaje.texto = "Nota Actualizada";
          this.showAlert();
          this.editar = false;
        })
        .catch((err) => console.log(err));
    },
  },
};
</script>
